.chronometer__table{
  overflow-x: auto;
}
.chronometer__table--info{
  padding: .75rem 1.25rem;
  margin-bottom: 0;
  background-color: #f8f9fc;
  border-bottom: 1px solid #e3e6f0;
  font-weight: bold;
  color: #4e73df;
}
.chronometer__table--add{
  font-weight: bold;
}
.chronometer__header{
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  align-items: center;
}

.chronometer__edit-mode--switch{
  margin:0
}

.chronometer__modal__body{
  text-align: center;
  font-size: 1.5rem;
}