
table {
    border-spacing: 0;
    margin: 0 auto;
    padding: 10px;
    color: #858796;
    background-color: #ffffff;
  }
  
  th{
    border-top: 1px solid #e3e6f0;
    border-bottom: 3px solid #e3e6f0;
  }
  
  td {
    border-bottom: 1px solid #e3e6f0;
  
  }
  
  td,th{
    border-right: 1px solid #e3e6f0;
    font-size: 16px;
    margin: 0;
    padding: 12px;
  }
  
  td:last-child, th:last-child{
    border-right: 0;
  }
  
  tr td input{
    display: block;
    margin:0 auto;
  }